<template>
	<view>
		
		<view v-if="list.length<1" style="text-align: center;width: 100vw;margin-top: 40vw;">
			<image src="/static/img/gg_01.png" style="width: 30vw;height: 30vw;"></image>
			<view style="margin-top: 2vw;">没有记录</view>
			</view>
		
		<view v-for="(item,index) in list" :key="index" class="cu-card article " style="margin:1rpx">
			<view @click="tiaozhuan_lishi_fankui([item.feedbackId])" class="cu-item shadow">
				<view class="title" >
					<view class="text-cut">{{item.feedbackTitle}}</view>
				</view>
				<view class="content" >
					<image :src="item.firstPhotoUrl" mode="aspectFill"></image>
					<view class="desc">
						<view class="text-content">
							<view>{{item.feedbackMsg}}</view>
						</view>	
						<view>
							<view v-if="item.state==0" class="cu-tag bg-green light sm round">未处理</view>
							<view v-if="item.state==1" class="cu-tag bg-green light sm round">处理中</view>
							<view v-if="item.state==2" class="cu-tag bg-green light sm round">已完成</view>
							
						</view>
					</view> 
				</view>
			</view>
		</view>
		
		<view @click="tiaozhuan_fankui()" style="width: 10vw;height: 10vw;right: 10vw;bottom: 10vw;color: #FFFFFF;background: #518bee;border-radius:50%;line-height: 10vw;text-align: center;font-size: 200%;" class="dingwei_juedui dibu">
			+
		</view>
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				list:[],
			}
		},
		methods: {
			tiaozhuan_fankui:function(){
				uni.navigateTo({
					url:"../fankui_ing/fankui_ing"
				})
			},
			huoqu_fankui_lishi:function(){
				let _this = this
				_this.wangluo_qingqiu('/wx/ipms/getPersonalFeedbackList', null, true, true, 'GET').then(value => {
					if (value) {
						_this.list = value
					}
				})
			},
			tiaozhuan_lishi_fankui:function(res){
				uni.navigateTo({
					url:"../fankui_lishi/fankui_lishi?id=" + res[0]
				})
				
				
				
				
			}
		},
		onLoad:function(){
		},
		onShow:function(){
			this.huoqu_fankui_lishi()
		}
	}
</script>

